.evaluation__label {
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
  line-height: 1.44;
  margin-left: 80px;
  text-transform: initial;
}
.evaluation__label::before {
  width: 56px;
  height: 56px;
  background-color: #f2f2f2;
  border-radius: 50%;
  content: "";
  left: 0;
  position: absolute;
  top: -13px;
}
.evaluation__radio:checked ~ .evaluation__label::after {
  width: 22px;
  height: 22px;
  background-color: black;
  border-radius: 50%;
  content: "";
  left: 17px;
  position: absolute;
  top: 4px;
}
.evaluation__radio:focus ~ .evaluation__label {
  border-radius: 5px;
  outline: 2px solid #81b3d2;
}
.evaluation__radio:hover ~ .evaluation__label::before,
.evaluation__radio:focus ~ .evaluation__label::before {
  background-color: #e5e5e5;
  border-color: #e5e5e5;
}
.evaluation__radio:active ~ .evaluation__label::before {
  background-color: #d5d5d5;
  border-color: #d5d5d5;
}
.evaluation__radio:active ~ .evaluation__label::after {
  background-color: rgba(51, 51, 51, 0.3);
}
.evaluation__radio:disabled ~ .evaluation__label {
  color: rgba(102, 102, 102, 0.3);
}
.evaluation__radio:disabled ~ .evaluation__label::before {
  background-color: rgba(102, 102, 102, 0.3);
  border-color: rgba(102, 102, 102, 0.3);
}
.evaluation__radio:disabled ~ .evaluation__label::after {
  background-color: rgba(102, 102, 102, 0.3);
}
