  font-size: 18px;
  font-weight: bold;
  line-height: 1.44;
  margin-bottom: 10px;
  margin-top: 18px;
  text-align: center;
}
.places__top {
  height: 160px;
}
.places__item {
  position: relative;
}
.places__item:not(:last-child) {
  margin-bottom: 54px;
}
.places__label {
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
  line-height: 1.44;
  margin-left: 80px;
  text-transform: initial;
}
.places__label::before {
  width: 56px;
  height: 56px;
  background-color: #f2f2f2;
  border: solid 5px #f2f2f2;
  border-radius: 5px;
  content: "";
  left: 0;
  position: absolute;
  top: -13px;
}
.places__checkbox:checked ~ .places__label::after {
  width: 24px;
  height: 16px;
  background: url("../../img/icon-tick.svg") no-repeat;
  content: "";
  left: 16px;
  position: absolute;
  top: 6px;
}
.places__checkbox:focus ~ .places__label {
  outline: 2px solid #81b3d2;
  border-radius: 5px;
}
.places__checkbox:hover ~ .places__label::before,
.places__checkbox:focus ~ .places__label::before {
  background-color: #e5e5e5;
  border-color: #e5e5e5;
}
.places__checkbox:active ~ .places__label::before {
  background-color: #d5d5d5;
  border-color: #d5d5d5;
}
.places__checkbox:active ~ .places__label::after {
  background: url("../img/icon-tick-four.svg") no-repeat;
}
.places__checkbox:disabled ~ .places__label {
  color: rgba(102, 102, 102, 0.3);
}
.places__checkbox:disabled ~ .places__label::before {
  background-color: rgba(102, 102, 102, 0.3);
  border-color: rgba(102, 102, 102, 0.3);
}
.places__checkbox:disabled ~ .places__label::after {
  width: 25px;
  height: 25px;
  top: 5px;
  background-color: #f5fffa;
  background-image: url("../img/icon-cross.svg") no-repeat;
}
